import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable,of} from 'rxjs';

import {Log} from '../models/Log';

@Injectable({
  providedIn: 'root'
})
export class LogService {
  logs: Log[];
  private logSource = new BehaviorSubject<Log>({id:null,text:null,date:null});
  selectedLog = this.logSource.asObservable();
  private stateSource = new BehaviorSubject<boolean>(true);
  stateClear = this.stateSource.asObservable();

  constructor() {
    this.logs=[];
    // [
    //   {id:'1',text:'Generated Logs',date:new Date('10/24/2020 11:00:00')},
    //   {id:'2',text:'About components',date:new Date('10/25/2020 21:00:00')},
    //   {id:'3',text:'This is angular component',date:new Date('10/26/2020 12:00:00')},
    //   {id:'4',text:'Generated data for cp',date:new Date('10/26/2020 23:00:00')}
    // ];
  }

  getLogs():Observable<Log[]>{
    if(localStorage.getItem('logs')===null){
      this.logs=[];
    }else{
      this.logs =JSON.parse( localStorage.getItem('logs'));
    }
    return of(this.logs.sort((a,b)=>{
      return b.date = a.date;
    }));
  }
  setFormLog(log:Log){
    this.logSource.next(log);
  }
  addLog(log:Log){
    this.logs.unshift(log);

    // Add to Local Storage;
    localStorage.setItem('logs',JSON.stringify(this.logs));
  }
  updateLog(log:Log){
    this.logs.forEach((cur,index)=>{
      if(log.id===cur.id){
        this.logs.splice(index,1);
      }
    });
    this.logs.unshift(log);

    // Update Local Storage;
    localStorage.setItem('logs',JSON.stringify(this.logs));
  }
  deleteLog(log:Log){
    this.logs.forEach((cur,index)=>{
      if(log.id===cur.id){
        this.logs.splice(index,1);
      }
    });
  // Delete Local Storage;
  localStorage.setItem('logs',JSON.stringify(this.logs));
  }
  clearState(){
    this.stateSource.next(true);
  }
}
